
a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.navbar {
  padding: 5px 5px;
  background: #fff;
  border: none;
  border-radius: 0;
  margin-bottom: 40px;
  -webkit-box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
          box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
}
i,
span {
  display: inline-block;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
}
#sidebar {
  min-width: 250px;
  max-width: 250px;
  background: #6382d7;
  color: #fff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
#sidebar.active {
  min-width: 80px;
  max-width: 80px;
  text-align: center;
}
#sidebar ul li a {
  text-align: left;
}
#sidebar.active ul li a {
  padding: 20px 10px;
  text-align: center;
  font-size: 0.85em;
}
#sidebar.active ul li a i {
  margin-right: 0;
  display: block;
  font-size: 1.8em;
  margin-bottom: 5px;
}
#sidebar.active ul ul a {
  padding: 10px !important;
}
#sidebar .sidebar-header {
  padding: 20px;
  background: #6382d7;
}
#sidebar ul.components {
  padding: 0 0;
}
#sidebar ul li a {
  padding: 20px;
  font-size: 1.1em;
  display: block;
}
#sidebar ul li a:hover {
  color: #7386D5;
  background: #fff;
}
#sidebar ul li a i, #sidebar .sidebar-header i {
  margin-right: 10px;
}
#sidebar ul li.active > a,
a[aria-expanded="true"] {
  color: #fff;
  background: #7d97de;
  border-left: 5px solid white;
}
a[data-toggle="collapse"] {
  position: relative;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
#content {
  width: 100%;
  min-height: 100vh;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
@media (max-width: 768px) {
#sidebar {
    min-width: 80px;
    max-width: 80px;
    text-align: center;
    margin-left: -80px !important;
}
#sidebar.active {
    margin-left: 0 !important;
}
#sidebar ul li a {
    padding: 20px 10px;
}
#sidebar ul li a span {
    font-size: 0.85em;
}
#sidebar ul li a i {
    margin-right: 0;
    display: block;
}
#sidebar ul ul a {
    padding: 10px !important;
}
#sidebar ul li a i {
    font-size: 1.3em;
}
#sidebar {
    margin-left: 0;
}
#sidebarCollapse span {
    display: none;
}
}
